.TabRegion {
  text-align: center;
  background-color: #fafafa;
  padding: 10px 0;
}
.TabRegion .updatePwd {
  width: 60%;
  margin: 0 auto;
}
.TabRegion .updatePwd .updateButton {
  background-color: #1aaa55;
  border-color: #168f48;
  color: #fff;
}
.TabRegion .updatePwd .updateButton:hover {
  box-shadow: 0 0 4px 2px #a4b999;
}
.TabRegion .updatePwd .forgetButton {
  background-color: #b2850d;
  border-color: #b2850d;
  color: #fff;
}
.TabRegion .updatePwd .forgetButton:hover {
  box-shadow: 0 0 4px 2px #a4b999;
}
